import Vue from 'vue';
import axios from 'axios';
import 'es6-promise/auto';

module.exports = new Vue({
  data: {
    // Json
    result:""
  },

  methods: {
    //  GET_AJAX_COMPLETE 
    getAjax(url, name){
      return axios.get(url)
      .then((res) => {
        // this.$set(this, name, res.data[0].name);
        this.$set(this, name, res.data);
        // $emit 
        this.$emit('GET_AJAX_COMPLETE');
      });
    },
    // 
    getData(name){
      return this.$data[name];
    },
  }
});
