exports.push([module.id, ".hello_footer[data-v-6c0a0fc1] {\n  color: #fcc;\n}\n@media screen and (max-width: 720px) {\n.hello_footer[data-v-6c0a0fc1] {\n    color: #0cc;\n}\n}\n.hello_footer2[data-v-6c0a0fc1] {\n  color: #f00;\n}\n", "",{"version":3,"sources":["D:/htdocs/vue/work/src/components/D:/htdocs/vue/work/src/components/D:/htdocs/vue/work/src/styles/todo.scss","D:/htdocs/vue/work/src/components/D:/htdocs/vue/work/src/components/D:/htdocs/vue/work/src/styles/_core/_mixin.scss","D:/htdocs/vue/work/src/components/D:/htdocs/vue/work/src/components/D:/htdocs/vue/work/src/components/app.vue"],"names":[],"mappings":"AAIE;EACE,WACF;AAAA;ACeE;ADbA;IACE,WACF;AAAA;AAAC;AEIH;EACE,WACF;AAAA","file":"app.vue?vue&type=style&index=0&id=6c0a0fc1&scoped=true&lang=scss&","sourcesContent":["@import \"./_core/_mixin\";\n@import \"./_core/_setting\";\n\n.hello {\n  &_footer{\n    color: #fcc\n  }\n  @include breakpoint(tab){\n    &_footer{\n      color: #0cc\n    }\n  }\n}\n","@charset \"utf-8\";\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin breakpoint($point){\n\n  @if $point == full{\n    @media screen and (max-width:$bp-pc + 40px){\n      @content;\n    }\n  }\n\n  @else if $point == pc{\n    @media screen and (max-width:$bp-pc - 1px){\n      @content;\n    }\n  }\n\n  @else if $point == tab{\n    @media screen and (max-width:$bp-tab){\n      @content;\n    }\n  }\n\n  @else if $point == sp{\n    @media screen and (max-width:$bp-sp){\n      @content;\n    }\n  }\n\n  @else{\n    @media screen and (max-width:$point){\n      @content;\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n// CSS\n//----------------------------------------------------------------\n// $img    : \n// $repeat    : background-repeat\n// $pos    : background-position\n// $format    : \n// $img_path    : \n// $width    : \n// $height    : \n// $width$heightscss\n// \n// $img_path$width$heightpx\n// $use-2x   :true setting.scss\n//----------------------------------------------------------------\n@mixin bg(\n  $img,\n  $repeat,\n  $pos,\n  $format,\n  $img_path:$cmn_img_dir,\n  $width:$img_width,\n  $height:$img_height,\n  $img-2x:$use-2x){\n  background-image: url(#{$img_path}#{$img}.#{$format});\n  background-repeat: #{$repeat};\n  background-position: #{$pos};\n\n  @if $img-2x{\n    @include background-size($width / 2, $height / 2);\n  }\n}\n\n\n//----------------------------------------------------------------\n// $pseudo    : before / after\n//----------------------------------------------------------------\n@mixin pseudo($pseudo:before, $display:inline-block, $width:auto, $height:auto, $pos:relative, $top:false, $right:false, $bottom:false, $left:false){\n  position:relative;\n  &:#{$pseudo}{\n    content:\"\";\n    display:#{$display};\n    width:#{$width};\n    height:#{$height};\n    overflow:hidden;\n    position:#{$pos};\n    @if $top{\n      top:#{$top};\n    }\n    @if $right{\n      right:#{$right};\n    }\n    @if $bottom{\n      bottom:#{$bottom};\n    }\n    @if $left{\n      left:#{$left};\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n//    \n//----------------------------------------------------------------\n// $pseudo    : before / after\n// $pos    : right / left / bottom / top\n// $width    : px\n// $color    : \n//----------------------------------------------------------------\n@mixin arrow($pseudo:before, $pos:right, $width:2.5px, $color:$gray){\n  position:relative;\n  &:#{$pseudo}{\n    content: \"\";\n    display:block;\n    position:absolute;\n    border-right:$width solid $color;\n    border-bottom:$width solid $color;\n    width:$width * 3;  // \n    height:$width * 3; // \n    @if $pos == 'right'{\n      transform: rotate(-45deg);\n    }\n    @if $pos == 'left'{\n      transform: rotate(135deg);\n    }\n    @if $pos == 'bottom'{\n      transform: rotate(45deg);\n    }\n    @if $pos == 'top'{\n      transform: rotate(-135deg);\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n//   \n//----------------------------------------------------------------\n// $pseudo    : before / after\n// $pos    : absolute / static\n// $direction    : up / right / down / left\n// $width    : px\n// $color    : \n//----------------------------------------------------------------\n@mixin arrow-triangle($pseudo, $pos, $direction, $width, $color){\n  @if $pos == 'absolute'{\n    position:relative;\n  }\n  &:#{$pseudo}{\n    content: \"\";\n    display:inline-block;\n    width:0;\n    height:0;\n    position:$pos;\n\n    // absolutedisplay\n    @if $pos == 'absolute'{\n      margin:0;\n      display:block;\n    }\n\n    // \n    @if $direction == 'up'{\n      border-width:0 $width $width $width;\n      border-color: transparent transparent $color transparent;\n    }\n    @if $direction == 'right'{\n      border-width:$width 0 $width $width;\n      border-color: transparent transparent transparent $color;\n    }\n    @if $direction == 'down'{\n      border-width:$width $width 0 $width;\n      border-color:$color transparent transparent transparent;\n    }\n    @if $direction == 'left'{\n      border-width:$width $width $width 0;\n      border-color:transparent $color transparent transparent;\n    }\n    border-style:solid;\n  }\n}\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n// %\n// $size    : \n// $context    : \n// \n// 1. 18px@include font-size(18)\n// 2. 18px12px\n// @include font-size(12, 18)\n//----------------------------------------------------------------\n// rem\n// $font-size    : \n// \n// 1. 13px@include font-size(13)\n//----------------------------------------------------------------\n@mixin font-size($size, $context:$default-font-size){\n\n  @if $font-percent{\n    font-size:percentage($size / $context);\n  }\n\n  @if $font-rem{\n    $rem: ($size / 10);\n    font-size: $size + px;\n    font-size: $rem +rem;\n  }\n\n  @if $font-px{\n    font-size: $size + px;\n  }\n}\n\n\n//----------------------------------------------------------------\n// clearfix\n//----------------------------------------------------------------\n@mixin clearfix{\n  &:before,\n  &:after {\n    content:\"\";\n    display:table;\n  }\n  &:after {\n    clear:both;\n  }\n}\n\n\n//----------------------------------------------------------------\n// 3\n//----------------------------------------------------------------\n@mixin ellipsis{\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin image-parallel{\n  @each $position in left, right{\n\n    .image-#{$position}{\n      overflow:hidden;\n\n      >.image{\n        float:#{$position};\n        text-align:center;\n\n        .caption{\n          margin-top:$margin / 3;\n          display:block;\n          @include font-size(12);\n        }\n\n        @if $position == left{\n          padding-right:percentage($col-space / $lyt-main);\n        }\n        @if $position == right{\n          padding-left:percentage($col-space / $lyt-main);\n        }\n      }\n\n      >.col{\n        overflow:hidden;\n      }\n\n      // \n      &.wrap{\n        >.image{\n          margin-bottom:percentage($col-space / $lyt-main);\n        }\n      }\n\n      // ====Media Queries====\n      @include breakpoint(full){\n        >.image{\n          max-width:50%;\n        }\n      }\n\n      @include breakpoint(tab){\n        >.image{\n          padding-left: 0;\n          padding-right: 0;\n          float:none;\n          max-width:none;\n          text-align: center;\n\n          .caption{\n            br{\n              display:none;\n            }\n          }\n        }\n      }\n      // ====Media Queries====\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin image-col-lyt{\n  @for $i from 2 through 4{\n    $col:($lyt-main - ($col-space * ($i - 1))) / $i;\n\n    .image-col-#{$i}{\n      margin-top: 0;\n      @extend %clearfix;\n      text-align: center;\n\n      >li{\n        margin-top: $margin;\n        margin-left:percentage($col-space / $lyt-main);\n        float:left;\n        box-sizing:border-box;\n        width:percentage($col / $lyt-main);\n\n        &:first-child{\n          margin-left:0;\n        }\n\n        &:nth-child(#{$i}n+1){\n          margin-left:0;\n          clear:left;\n        }\n\n        li{\n          margin-left:0;\n          width:auto;\n          float:none;\n        }\n\n        .caption{\n          margin-top:5px;\n          display:block;\n          @include font-size(12);\n        }\n      }\n\n      // ====Media Queries====\n      @include breakpoint(sp){\n        >li{\n          margin-left:0;\n          float:none;\n          text-align: center;\n        }\n      }\n      // ====Media Queries====\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin list-col-lyt{\n  @for $i from 2 through 4{\n    $col:($lyt-main - ($col-space * ($i - 1))) / $i;\n\n    .list-col-#{$i}{\n      margin-top: 0;\n      @extend %clearfix;\n\n      >li{\n        margin-top: $margin;\n        margin-left:percentage($col-space / $lyt-main);\n        float:left;\n        box-sizing:border-box;\n        width:percentage($col / $lyt-main);\n\n        &:first-child{\n          margin-left:0;\n        }\n\n        &:nth-child(#{$i}n+1){\n          margin-left:0;\n          clear:left;\n        }\n\n        li{\n          margin-left:0;\n          width:auto;\n          float:none;\n        }\n      }\n\n      // ====Media Queries====\n      @include breakpoint(sp){\n        >li{\n          margin-left:0;\n          float:none;\n        }\n      }\n      // ====Media Queries====\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin col-lyt{\n  @for $i from 2 through 4{\n    $col:($lyt-main - ($col-space * $i)) / $i;\n\n    .col-lyt-#{$i}{\n      overflow: hidden;\n\n      >.col-lyt-inner{\n        margin-left: - (percentage($col-space / $lyt-main));\n        width: 100% + (percentage($col-space / $lyt-main));\n        @extend %clearfix;\n\n        >.col{\n          margin-left:percentage($col-space / $lyt-main);\n          float:left;\n          box-sizing:border-box;\n          width:percentage($col / $lyt-main);\n\n          &:nth-child(#{$i}n+1){\n            clear:left;\n          }\n        }\n      }\n\n\n      // ====Media Queries====\n      @include breakpoint(900px){\n        >.col-lyt-inner{\n          >.col{\n            width:percentage((($lyt-main - ($col-space * 2)) / 2) / $lyt-main);\n\n            &:nth-child(2n+1){\n              clear:left;\n            }\n            &:nth-child(#{$i}n+1){\n              clear:none;\n            }\n          }\n        }\n      }\n      @include breakpoint(sp){\n        >.col-lyt-inner{\n          margin-left: 0;\n          width: auto;\n\n          >.col{\n            margin-left: 0;\n            float: none;\n            width: auto;\n          }\n        }\n      }\n      // ====Media Queries====\n    }\n  }\n}\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin grid($grid-num:12){\n\n  /*\t12 COLUMN : RESPONSIVE GRID SYSTEM\n  DEVELOPER : DENIS LEBLANC\n  URL : http://responsive.gs\n  VERSION : 3.0\n  LICENSE : GPL & MIT */\n\n  // SET ALL ELEMENTS TO BOX-SIZING : BORDER-BOX\n  [class^=\"grid-\"]{\n    box-sizing:border-box;\n  }\n\n  // \n  .grid-container{\n    margin:0 auto;\n    max-width:$lyt-cont;\n    @extend %clearfix;\n\n    // clearfix\n    .grid-row,\n    .grid-col,\n    .grid-clr,\n    .grid-group{\n      @extend %clearfix;\n    }\n\n    // \n    .grid-col{\n      display:block;\n      float:left;\n    }\n\n    // \n    .grid-gutters{\n      .grid-col{\n        margin-left:percentage($col-space / $lyt-main);\n        &:first-child{\n          margin-left:0;\n        }\n      }\n    }\n\n    // \n    // \n    @for $i from 1 through $grid-num{\n      .grid-span-#{$i}{width:percentage($i / $grid-num);}\n    }\n\n    // \n    .grid-gutters{\n      @for $i from 1 through $grid-num{\n        $gutter:percentage($col-space / $lyt-main);\n        .grid-span-#{$i}{width:((100% - ((($grid-num / $i) - 1) * $gutter)) / $grid-num) * $i;}\n      }\n    }\n\n    // ====Media Queries====\n    @include breakpoint(sp){\n      .grid-col {\n        width:100% !important;\n        float:none;\n      }\n      .grid-gutters{\n        .grid-col{\n          margin-left:0;\n        }\n      }\n    }\n    // ====Media Queries====\n  }\n}\n\n\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin mp-control{\n\n  @for $i from 0 through 6{\n    $number:($i * 5);\n\n    @each $position in top, right, bottom, left{\n      //\n      .m-#{$position}-#{$number}{\n        @if $number == 0{\n          margin-#{$position}:0 !important;// 0px\n        }@else{\n          margin-#{$position}:$i * 5px !important;\n        }\n      }\n    }\n\n    @each $position in top, right, bottom, left{\n      //\n      .p-#{$position}-#{$number}{\n        @if $number == 0{\n          padding-#{$position}:0 !important;// 0px\n        }@else{\n          padding-#{$position}:$i * 5px !important;\n        }\n      }\n    }\n  }\n}\n\n\n//----------------------------------------------------------------\n// Width\n//----------------------------------------------------------------\n@mixin width-control{\n  @for $i from 1 through 20{\n    $w:($i * 5);\n    .w-#{$w} { width: 5% * $i !important;}\n  }\n}\n\n\n//----------------------------------------------------------------\n// Line\n//----------------------------------------------------------------\n@mixin line-control{\n  @each $position in top, right, bottom, left{\n    .line-#{$position}{border-#{$position}:1px #000 solid !important;}\n    .line-reset-#{$position}{border-#{$position}:none !important;}\n  }\n}\n\n\n//----------------------------------------------------------------\n// Display\n//----------------------------------------------------------------\n@mixin display-control{\n  @each $display in block, inline-block, inline{\n    .#{$display}{display:#{$display} !important;}\n  }\n}\n\n\n//----------------------------------------------------------------\n// font-awesome-def\n//----------------------------------------------------------------\n// \n//----------------------------------------------------------------\n@mixin font-awesome-def{\n  line-height:1;\n  font-family: FontAwesome;\n  font-weight: normal;\n  font-style: normal;\n  text-decoration:none;\n  text-align:left;\n  speak:none;\n}","\n\n\n\n\n\n\n\n\n\n\n\n@import \"../styles/todo.scss\";\n.hello {\n  &_footer2{\n    color: #f00\n  }\n}\n"],"sourceRoot":""}]);

